<template>
  <v-row class="ma-sm-4">
    <v-col>
      <v-card
        flat
        class="px-4 pb-2"
      >
        <v-card-title>
          <v-row>
            <v-col
              cols="12"
              sm="6"
            >
              Админы:
            </v-col>
            <v-col class="text-right">
              <v-dialog
                v-model="dialog"
                persistent
                max-width="500px"
              >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn
                    depressed
                    color="primary"
                    class="text-none"
                    large
                    height="36"
                    v-bind="attrs"
                    v-on="on"
                  >
                    Создать
                  </v-btn>
                </template>

                <v-card>
                  <v-card-title>
                    <span class="text-h5">{{ formTitle }}</span>
                  </v-card-title>

                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col
                          cols="12"
                          sm="6"
                        >
                          <v-text-field
                            v-model="editedAdmin.username"
                            hide-details="auto"
                            label="Имя"
                          />
                        </v-col>
                        <v-col
                          cols="12"
                          sm="6"
                        >
                          <v-text-field
                            v-model="editedAdmin.password"
                            :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
                            :type="show ? 'text' : 'password'"
                            hide-details="auto"
                            label="Пароль"
                            @click:append="show = !show"
                          />
                        </v-col>
                        <v-col cols="12">
                          <v-select
                            v-model="editedAdmin.role"
                            :items="roles"
                            hide-details="auto"
                            clearable
                            label="Роль"
                          />
                        </v-col>
                        <v-col cols="12">
                          <v-select
                            v-model="editedAdmin.permissions"
                            :items="permissionsItems"
                            hide-details="auto"
                            clearable
                            label="Права"
                            multiple
                          >
                            <template v-slot:selection="{ item, index }">
                              <v-chip v-if="index === 0">
                                <span>{{ item }}</span>
                              </v-chip>
                              <span
                                v-if="index === 1"
                                class="grey--text text-caption"
                              >
                                (+{{ editedAdmin.permissions.length - 1 }} others)
                              </span>
                            </template>
                          </v-select>
                        </v-col>
                      </v-row>
                    </v-container>
                  </v-card-text>

                  <v-card-actions>
                    <v-spacer />

                    <v-btn
                      text
                      color="primary"
                      @click="close"
                    >
                      Отмена
                    </v-btn>
                    <v-btn
                      text
                      color="primary"
                      @click="close"
                    >
                      {{ buttonText }}
                    </v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-col>
          </v-row>
        </v-card-title>
        <v-data-table
          :headers="headers"
          :items="items"
          :items-per-page="5"
          no-data-text="Админы отсутствуют"
        >
          <template v-slot:[`item.permissions`]="{ item }">
            <v-chip v-if="item.permissions[0]">
              <span>{{ item.permissions[0] }}</span>
            </v-chip>
            <span v-else>
              {{ item.role === 'SUPERUSER' ? 'Все' : 'Отсутствуют' }}
            </span>
            <span
              v-if="item.permissions[1]"
              class="grey--text text-caption"
            >
              (+{{ item.permissions.length - 1 }} others)
            </span>
          </template>

          <template v-slot:[`item.createdAt`]="{ item }">
            {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
          </template>

          <template v-slot:[`item.action`]="{ item }">
            <v-btn
              depressed
              class="text-none"
              large
              height="36"
              @click="editAdmin(item)"
            >
              Редактировать
            </v-btn>
          </template>
        </v-data-table>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
export default {
  data() {
    return {
      dialog: false,
      editedIndex: -1,
      editedAdmin: {},
      show: false,
      roles: ['ROLE_UNDEFINED', 'USER', 'ADMINISTRATOR', 'SUPERUSER', 'PARTNER', 'BLOGGER', 'BANNED', 'PUBLIC'],
      permissionsItems: ['DASHBOARD_ADMINISTRATION', 'CHAT_ADMINISTRATION', 'BONUS_ADMINISTRATION', 'USERS_ADMINISTRATION', 'WITHDRAW_ADMINISTRATION', 'GAMES_ADMINISTRATION'],
      headers: [
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'Имя:', value: 'username', class: 'white--text text-subtitle-2' },
        { text: 'Роль:', value: 'role', class: 'white--text text-subtitle-2' },
        { text: 'Права:', value: 'permissions', class: 'white--text text-subtitle-2' },
        { text: 'Дата регистрации:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
      items: [
        {
          id: 1,
          username: 'Test',
          role: 'SUPERUSER',
          permissions: [],
          createdAt: 3333333333333,
        },
        {
          id: 2,
          username: 'Admin',
          role: 'ADMINISTRATOR',
          permissions: ['DASHBOARD_ADMINISTRATION', 'CHAT_ADMINISTRATION', 'BONUS_ADMINISTRATION'],
          createdAt: 981267972163,
        },
      ],
    };
  },
  computed: {
    formTitle() {
      return this.editedIndex === -1 ? 'Создать админа' : 'Редактирование';
    },
    buttonText() {
      return this.editedIndex === -1 ? 'Создать' : 'Сохранить';
    },
  },
  methods: {
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedIndex = -1;
        this.editedAdmin = {};
      });
    },
    editAdmin(item) {
      this.editedIndex = item.id;
      this.editedAdmin = { ...item };
      this.dialog = true;
    },
  },
};
</script>
